body {
  padding-top: 90px;
}
.v-header {
  position: relative;
  position: fixed;
  top: 0;
  width: 100%;
  height: 90px;
  border-bottom: 1px solid gray;
  text-align: center;
  line-height: 90px;
}
.v-header .name {
    padding-right: 15px;
    padding-left: 15px;
}
.v-header .name h1 a {
      font-size: 40px;
      color: #333;
      text-decoration: none;
}
.v-header .name p {
      color: #777;
      padding-top: 10px;
      margin-left: 10px;
}
.v-header .name p span {
        font-size: 1.5em;
}
@media screen and (max-width: 400px) {
.v-header .name p {
        font-size: 12px;
}
.v-header .name h1 a {
        font-size: 30px;
}
}
.v-header .messages {
    padding-right: 15px;
    padding-left: 15px;
}
.v-header .messages ul li {
      display: inline-block;
      margin-left: 20px;
}
.v-header .messages ul li img {
        width: 30px;
        height: auto;
        vertical-align: middle;
}
.v-header .messages ul li.blog a {
        padding: 5px;
        border: 1px solid gray;
        border-radius: 6px;
        background-color: #ddd;
}
.v-header .messages span {
      display: none;
      text-indent: -9999px;
}
@media screen and (max-width: 980px) {
.v-header .messages ul {
        display: none;
        background-color: #fff;
        text-align: center;
        position: absolute;
        top: 90px;
        right: 15px;
}
.v-header .messages ul li {
        display: block;
        border-bottom: 1px solid gray;
}
.v-header .messages span {
        display: block;
        width: 30px;
        height: 30px;
        background-color: #fff;
        position: absolute;
        top: 30px;
        right: 20px;
}
.v-header .messages span .menu-icon {
          width: 30px;
          height: 30px;
          background-color: red;
          position: absolute;
          top: 0;
          border-top: 5px solid gray;
          border-bottom: 5px solid gray;
          background-color: gray;
          padding: 7px 0;
          background-clip: content-box;
}
.v-header .messages span .menu-icon:hover {
            border-top: 5px solid #DD575C;
            border-bottom: 5px solid #DD575C;
            background-color: #DD575C;
}
}
